import sf from '@spiral-toolkit/core';
import { GridRenderer } from './render/GridRenderer';
import { RequestMethod } from './constants';
import { defaultRenderer } from './render/defaultRenderer';
import * as assert from 'assert';
// import './styles.css';
export class Datagrid extends sf.core.BaseDOMConstructor {
    constructor(sf, node, options) {
        super();
        this.options = Datagrid.optionsToGrab;
        this.grids = [];
        this.init(sf, node, options);
        this.createRenderers();
    }
    createRenderers() {
        assert.deepEqual(this.grids, [], 'Grids are already initialized');
        const renderList = Array.isArray(this.options.renderers) ? this.options.renderers : [this.options.renderers];
        renderList.forEach((renderOption) => {
            new GridRenderer(renderOption, this);
        });
    }
    render(data) {
        const renderList = Array.isArray(this.options.renderers) ? this.options.renderers : [this.options.renderers];
        renderList.forEach((renderOption) => {
            console.log(renderOption);
        });
    }
}
Datagrid.spiralFrameworkName = 'datagrid';
Datagrid.optionsToGrab = {
    captureForms: [],
    columns: [],
    headers: {},
    method: RequestMethod.GET,
    sortable: [],
    url: '',
    ui: {
        cellAttributes: {},
        rowAttributes: {},
        rowClassName: '',
        cellClassName: '',
        tableClassName: '',
    },
    renderers: defaultRenderer,
};
export default Datagrid;
